@font-face {
  font-family: 'Inter';
  font-style:  normal;
  font-weight: 400;
  font-display: swap;
  src: url(/around-react_ptbr/static/media/Inter-Regular.dc131113.woff2) format("woff2"),
       url(/around-react_ptbr/static/media/Inter-Regular.3ac83020.woff) format("woff");
}

@font-face {
  font-family: 'Inter';
  font-style:  normal;
  font-weight: 500;
  font-display: swap;
  src: url(/around-react_ptbr/static/media/Inter-Medium.75db5319.woff2) format("woff2"),
       url(/around-react_ptbr/static/media/Inter-Medium.c0638bea.woff) format("woff");
}

@font-face {
  font-family: 'Inter';
  font-style:  normal;
  font-weight: 900;
  font-display: swap;
  src: url(/around-react_ptbr/static/media/Inter-Black.661569af.woff2) format("woff2"),
       url(/around-react_ptbr/static/media/Inter-Black.d0b121f3.woff) format("woff");
}
.page {
  display: flex;
  flex-direction: column;
  font-family: "Inter", sans-serif;
}

.header {
  max-width: 880px;
  width: 100%;
  min-height: 56px;
  margin: auto;
  padding-top: 45px;
  border-bottom: 1px solid #545454;
}

@media screen and (max-width: 960px) {
  .header {
    max-width: none;
    min-width: 56px;
    padding-top: 28px;
  }
}

@media screen and (max-width: 960px) {
  .header__logo {
    width: 126px;
    height: 24px;
    padding-left: 27px;
  }
}
.content {
  display: flex;
  flex-direction: column;
  max-width: 880px;
  width: 100%;
  margin: auto;
}

@media screen and (max-width: 960px) {
  .content {
    max-width: 282px;
  }
}

.profile {
  display: grid;
  grid-template-columns: repeat(2, auto) 1fr;
  grid-template-rows: 1fr;
  align-items: center;
  width: 100%;
  padding-top: 40px;
}

@media screen and (max-width: 960px) {
  .profile {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-top: 42px;
  }
}
.profile__avatar-container {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  width: 120px;
  height: 120px;
  border-radius: 50%;
  overflow: hidden;
}

.profile__avatar {
  width: 100%;
  height: 100%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.profile__avatar-overlay {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  background-image: url(/around-react_ptbr/static/media/icon_change-avatar.34c46850.svg);
  background-repeat: no-repeat;
  background-position: center;
  opacity: 0;
}

.profile__avatar-overlay:hover {
  opacity: 1;
  cursor: pointer;
}

.profile__info {
  display: grid;
  grid-template-columns: repeat(2, auto);
  grid-template-rows: repeat(2, auto);
  grid-template-areas: 
    "name button"
    "description description";
  padding-left: 30px;
}

@media screen and (max-width: 960px) {
  .profile__info {
    padding: 30px 0 36px;
  }
}
.profile__name {
  padding-right: 18px;
  font-size: 42px;
  line-height: 48px;
  font-weight: 500;
  grid-area: name;
}

@media screen and (max-width: 960px) {
  .profile__name {
    font-size: 22px;
    line-height: normal;
  }
}
.profile__description {
  font-size: 18px;
  font-weight: 400;
  padding-top: 16px;
  grid-area: description;
}

@media screen and (max-width: 960px) {
  .profile__description {
    font-size: 14px;
    padding-top: 14px;
    text-align: center;
  }
}
.profile__button-add {
  justify-self: end;
  width: 150px;
  height: 50px;
  background-image: url(/around-react_ptbr/static/media/icon_add.bdfb8ebb.svg);
  background-repeat: no-repeat;
  background-position: center;
}

@media screen and (max-width: 960px) {
  .profile__button-add {
    width: 100%;
  }
}
.profile__button-edit {
  position: relative;
  top: 17px;
  grid-area: button;
  width: 24px;
  height: 24px;
  background-image: url(/around-react_ptbr/static/media/icon_edit.58afbe73.svg);
  background-repeat: no-repeat;
  background-position: center;
}

@media screen and (max-width: 960px) {
  .profile__button-edit {
    top: calc(100% - 24px);
    width: 18px;
    height: 18px;
  }
}
.button {
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid #fff;
  background-color: transparent;
  box-sizing: border-box;
  cursor: pointer;
  -webkit-tap-highlight-color: transparent;
}

.button:hover {
  opacity: .6;
}

@media screen and (max-width: 960px) {
  .button:hover {
    opacity: 1;
  }
}
.cards {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 20px 17px;
  gap: 20px 17px;
  list-style: none;
  padding-top: 50px;
}

@media screen and (max-width: 960px) {
  .cards {
    grid-template-columns: 1fr;
    padding-top: 36px;
    -moz-column-gap: 0;
         grid-column-gap: 0;
         -webkit-column-gap: 0;
                 column-gap: 0;
  }
}
.card {
  position: relative;
  display: grid;
  grid-template-rows: 282px 1fr;
  grid-template-areas:
    "image"
    "info";
  width: 282px;
  height: 361px;
  border-radius: 10px;
  background-color: white;
  overflow: hidden;
}

.card__image {
  grid-area: image;
  width: 282px;
  height: 282px;
  cursor: pointer;
  border-bottom: 2px solid black;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.card__info-container {
  position: relative;
  grid-area: info;
}

.card__title {
  max-width: 220px;
  position: absolute;
  left: 21px;
  top: 25px;
  font-size: 24px;
  font-weight: 900;
  color: #000;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

.card__like-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  grid-gap: 3px;
  gap: 3px;
  position: absolute;
  right: 18px;
  height: 100%;
}

.card__like-counter {
  color: black;
  font-size: 13px;
  font-weight: 400;
  text-align: center;
}

.card__like-button {
  background-image: url(/around-react_ptbr/static/media/icon_like-inactive.94185013.svg);
  background-size: contain;
  background-repeat: no-repeat;
  background-position: bottom;
  border: 0;
  width: 21px;
  height: 21px;
  cursor: pointer;
}

.card__button-like_active {
  background-image: url(/around-react_ptbr/static/media/icon_like-active.a5ea67e3.svg);
}
.card__button-delete {
  position: absolute;
  border: 0;
  right: 15px;
  top: 18px;
  width: 18px;
  height: 19px;
  background-image: url(/around-react_ptbr/static/media/icon_delete.1dddd324.svg);
  background-size: contain;
}
.card__button-delete_hidden {
  visibility: hidden;
}

.footer {
  max-width: 880px;
  width: 100%;
  margin: auto;
  padding: 67px 0 60px;
}

@media screen and (max-width: 960px) {
  .footer {
    max-width: 282px;
    padding: 50px 0 36px;
  }
}
.footer__text {
  color: #545454;
  font-size: 18px;
  font-weight: 400;
}

@media screen and (max-width: 960px) {
  .footer__text {
    font-size: 14px;
  }
}
.popup {
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  z-index: 3;
  visibility: hidden;
  opacity: 0;
  transition: opacity .3s, visibility .3s;
}
.popup__overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: black;
  opacity: .5;
}
.popup_visible {
  opacity: 1;
  visibility: visible;
}
.popup__button-close {
  position: absolute;
  border: 0;
  right: -40px;
  top: -40px;
  width: 36px;
  height: 36px;
  background-image: url(/around-react_ptbr/static/media/icon_close.c7df0548.svg);
  background-size: contain;
}

@media screen and (max-width: 960px) {
  .popup__button-close {
    width: 20px;
    height: 20px;
    right: 0;
    top: -39px;
  }
}
.form {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 430px;
  z-index: 5;
  background-color: #fff;
  padding: 34px 36px 36px;
  box-sizing: border-box;
  border-radius: 10px;
}

@media screen and (max-width: 960px) {
  .form {
    justify-content: space-between;
    width: 282px;
    height: 332px;
    padding: 25px 22px;
  }
}

.form__header {
  max-height: 23px;
  font-size: 24px;
  font-weight: 900;
  color: #000;
  padding-bottom: 38px;
}

@media screen and (max-width: 960px) {
  .form__header {
    font-size: 18px;
    padding-bottom: 60px;
  }
}

.form__input-area {
  position: relative;
  width: 100%;
  padding-top: 16px;
  margin-bottom: 14px;
}

.form__input-area:last-of-type {
  margin-bottom: 48px;
}

@media screen and (max-width: 960px) {
  .form__input-area:last-of-type {
    margin-bottom: 46px;
  }
}

.form__field {
  width: 100%;
  padding-bottom: 13px;
  box-sizing: border-box;
  font-size: 14px;
  font-weight: 400;
  border: 0;
  border-bottom: 1px solid rgba(0, 0, 0, .2);
}

.form__field:-webkit-autofill,
.form__field:-webkit-autofill:hover,
.form__field:-webkit-autofill:focus,
.form__field:-webkit-autofill:active {
  -webkit-transition: background-color 9999s ease-in-out 0s;
  transition: background-color 9999s ease-in-out 0s;
}
.form__field_type_error {
  border-color: #ff0000;
}
.form__button-submit {
  min-height: 50px;
  background-color: #000;
  color: #fff;
  border: 0;
  font-size: 18px;
  font-weight: 400;
}

.form__button-submit:hover {
  opacity: .8;
  cursor: pointer;
}

@media screen and (max-width: 960px) {
  .form__button-submit {
    font-size: 14px;
  }
}
.form__button-submit_inactive {
  background-color: transparent;
  opacity: .2;
  border: 1px solid #000;
  color: #000;
  pointer-events: none;
}
.form__input-error {
  width: 100%;
  position: absolute;
  top: calc(100% + 5px);
  left: 0;
  font-size: 12px;
  font-weight: 400;
  color: #ff0000;
  overflow-wrap: break-word;
  opacity: 0;
  transition: .3s;
}
.form__input-error_active {
  opacity: 1;
}
.image-popup {
  position: relative;
  z-index: 5;
}
.image-popup__title {
  position: relative;
  padding-top: 10px;
  font-size: 12px;
  font-weight: 400;
}
.image-popup__image {
  max-width: 75vw;
  width: 100%;
  max-height: 75vh;
  height: 100%;
  object-fit: contain;
}


